//----------------------------
//LISTS VARIABLES
//----------------------------

$margin-list: 2rem 0;
$padding-list: 20%;
$margin-list-li: 0.5em 0;

//UNORDERED - UL
// $padding-list-unordered:20%;
$margin-list-unordered-li: 0.75rem 0;

//ORDERED
// $padding-list-ordered:20%;
$margin-list-ordered-li: 0.75rem 0;

//ALTERNATE - UL.ALTERNATE
// $padding-list-alternate:1em;
$margin-list-alternate-li: 0.75rem 0;

//ICONS
$margin-list-icon-li: 0 0 0 2rem;
$list-style-icon: none;
$display-list-icon: flex;
$flex-direction-icon: row;

//DEFINITIONS
// $margin-list-definition: 2rem 0;
// $padding-list-definition: 0;
// $padding-list-definition-p: 1rem 0;
// $margin-list-definiton-p: 0 56rem;
// $line-height-definition-p: 2.5rem;

$text-transform-title: capitalize;
$list-style-item: none;
$padding-item: 0;
$margin: 1em 0;

$margin-left: 2em;
//BLOCKQUOTE
$font-style-blockquote: italic;
$margin-blockquote: 2rem 0;
$border-blockquote: solid 3px $color-neutral-5;
$padding-blockquote: 0 1.5rem;
$line-height-blockquote: 2.5rem;

//----------------------------
//LISTS SPECIFICATIONS
//----------------------------

//BLOCKQUOTE
.blockquote {
    font-style: $font-style-blockquote;
    margin: $margin-blockquote;
    border-left: $border-blockquote;
    padding: $padding-blockquote;
    line-height: $line-height-blockquote;
}
//DEFINITION
.definition {
    list-style: none;
    padding: 0;
    margin: $margin;
    .text-definition {
        margin-left: 2rem;
    }
}
