.list-unordered {
    // margin: $margin-list;
    li {
        margin: $margin-list-unordered-li;
    }
}

.list-ordered {
    // padding-left:$padding-list-ordered;
    // margin:$margin-list;

    li {
        margin: $margin-list-ordered-li;
    }
}

.list-alternate {
    li {
        margin: $margin-list-alternate-li;
    }
}

