// ICON
$padding-list-icon: 0;
$margin-list-list-icon--li: 0 0.5em;
$list-style-list-icon: none;
$display-list-icon: flex;
$flex-direction-list-icon: row;

.list-icon {
    display: $display-list-icon;
    flex-direction: $flex-direction-list-icon;
    padding-left: $padding-list-icon;
    margin: $margin-list;
    list-style: $list-style-list-icon;

    li {
        margin: $margin-list-list-icon--li;
    }
    li:last-of-type {
        padding: 0 1rem 0 0;
    }
    .icon {
        &:hover {
            color: $colorHover;
        }
    }
}
